<template>
  <div class="creatproject">
    <div class="contain-center edit-page">
      <span class="back-tag" @click="goBack"><i class="el-icon-arrow-left"></i></span>
      <div class="main-box">
        <div class="left-wrap">
          <!--==================================================================-->
          <div class="item-block" style="margin-top:0;">
            <div class="block-tt-line">
              <span class="b-title"></span>
              <span class="b-line"></span>
            </div>
            <div class="block-info block-cc-file">
              <span class="f-title fl"></span>
              <!--<span class="f-name"></span>
              <span class="del-btn"><i class="el-icon-delete"></i></span>-->
              <span style="margin-left: 20px;" class="fl">
                <el-upload class="Upload"
                           ref="upload"
                           action="/project/projectUpload"
                           :on-change="planChange"
                           :on-success="planuploadsuccess"
                           :on-preview="planPreview"
                           :on-remove="planRemove"
                           :on-error="planuploaderror"
                           :file-list="planList"
                           :before-upload="beforeUpload"
                           accept=".doc, .ppt, .pdf, .zip, .rar, .docx, .pptx"
                           :data="uploadDate">
                  <el-button slot="trigger" type="primary" v-show="planButton" class="fl"><i class="el-icon-plus"></i></el-button>
                  <div slot="tip" class="el-upload__tip fr" v-show="planButton">BPpdfpptdocpngjpgjpeg</div>
                </el-upload>
              </span>

            </div>
            <el-form :model="project" ref="project" label-width="100px" class="padding" label-position="top">
              <el-row :span="24" :gutter="10">
                <el-col :span="12">
                  <span class="justIlook">()</span>
                  <el-form-item
                    label=""
                    prop="pro_name">
                    <el-input v-model="project.pro_name" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <span class="justIlook2">()</span>
                  <el-form-item
                    label=""
                    prop="pro_company_name">
                    <el-autocomplete v-model="project.pro_company_name"
                                     :fetch-suggestions="querySearchAsync"
                                     placeholder=""
                                     @select="handleSelect" class="width360">

                    </el-autocomplete>
                  </el-form-item>
                </el-col>
                <span class="ques">
                  <el-tooltip placement="bottom-end">
                      <div slot="content">
                        <div class="tips-txt">1</div>
                        <div class="tips-txt" style="margin-top:5px;">2</div>
                      </div>
                      <img src="../../../assets/images/question.png" alt="" />
                  </el-tooltip>
              </span>
              </el-row>
            </el-form>
          </div>
          <!--==================================================================-->
          <div class="item-block" style="margin-top:16px;">
            <div class="block-tt-line">
              <span class="b-title"></span>
              <span class="b-line"></span>
            </div>
            <el-form :model="project" ref="project" label-width="100px" class="padding" label-position="top">
              <el-row :span="24" :gutter="32">
                <el-col :span="12">
                  <el-form-item
                    label=""
                    prop="pro_intro"
                    :rules="[{required: true, message: '', trigger: 'blur'}]">
                    <el-input v-model="project.pro_intro" placeholder="FA"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item
                    label=""
                    prop="pro_industry"
                    :rules="[{type: 'array',required: true, message: '', trigger: 'change'}]">
                    <el-select
                      v-model="project.pro_industry"
                      multiple
                      filterable
                      allow-create
                      :multiple-limit="multiplelimit"
                      placeholder="(5)" class="width360" >
                      <el-option
                        v-for="item in industry"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row :span="24" :gutter="32">
                <el-col :span="12">
                  <el-form-item
                    label=""
                    prop="pro_finance_stage"
                    :rules="[{type:'number',required: true, message: '', trigger: 'change'}]">
                    <el-select v-model="project.pro_finance_stage" placeholder="" class="width360">
                      <el-option
                        v-for="item in stage"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="6">
                  <el-form-item
                    label=""
                    prop="pro_area_province"

                    :rules="[{required: true, message: '', trigger: 'change',type: 'number'}]">
                    <el-select v-model="project.pro_area_province" placeholder="" @change="area1Change">
                      <el-option
                        v-for="item in area"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                      >
                      </el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="6">
                  <el-form-item label=""
                                prop="pro_area_city"
                                :rules="[{required: true, message: '', trigger: 'change',type: 'number'}]">
                    <el-select v-model="project.pro_area_city" placeholder="">
                      <el-option
                        v-for="item in area2"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row :span="24" :gutter="32">
                <el-col :span="12">
                  <el-form-item
                    label=""
                    prop="pro_finance_scale"
                    :rules="[{required: true, message: '', trigger: 'change',type: 'number'}]">
                    <el-select v-model="project.pro_finance_scale" placeholder="" class="width360">
                      <el-option
                        v-for="item in scale"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item
                    label="(%)"
                    prop="pro_finance_stock_after">
                    <el-input v-model="project.pro_finance_stock_after" placeholder="10"></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row :span="24" :gutter="32">
                <el-col :span="12">
                  <el-form-item
                    label=""
                    prop="open_status">
                    <el-select v-model="project.open_status" placeholder="" class="width360">
                      <el-option label="" value="0"></el-option>
                      <el-option label="" value="1"></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item
                    label=""
                    prop="is_exclusive">
                    <el-radio class="radio" v-model="project.is_exclusive" :label=2>FA</el-radio>
                    <el-radio class="radio" v-model="project.is_exclusive" :label=1>FA</el-radio>
                    <el-radio class="radio" v-model="project.is_exclusive" :label=0></el-radio>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row :span="24" :gutter="32">
                <el-col :span="24">
                  <el-form-item label=""
                                prop="pro_goodness"
                                :rules="[{required: true, message: '', trigger: 'blur'}]">
                    <el-input type="textarea"
                              v-model="project.pro_goodness"
                              :autosize="{ minRows: 4, maxRows: 7}"></el-input>
                  </el-form-item>
                </el-col>
              </el-row>

            </el-form>
          </div>
          <el-button type="primary" size="large" style="float: right;" @click="allSave"></el-button>
          <div style="height: 50px;"></div>
          <el-dialog
            :title="companyTitle"
            :visible.sync="dialogVisible"
            size="tiny"
            :show-close="close">
            <span></span>
            <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="sync"></el-button>
          </span>
          </el-dialog>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data () {
    return {
      planList:[],//
      planButton:false,//
      dialogVisible:false,//
      uploadShow:{//,

      },
      close:false,
      uploadDate:{user_id: sessionStorage.user_id},//
      project: {
        project_id:'',
        pro_name : '',//
        pro_company_name : '',//
        pro_intro : '',//
        pro_industry:[],//
        pro_finance_stage: {
          "stage_id": 2,
          "stage_name": "",
          "sort": 2,
          "created_at": null,
          "updated_at": null
        },//
        pro_area_province:"",//1
        pro_area_city:"",//2
        pro_finance_scale:'',//
        pro_finance_stock_after:'',//
        open_status:'1',//
        is_exclusive: 0,//0 1 2
        pro_goodness:''//

      },//
      multiplelimit:5,
      /**/
      loading: false,
      restaurants: [],
      timeout:  null,
      /*1*/
      area: [],
      /*2*/
      area2: [],
      /**/
      industry: [],
      /**/
      scale:[],
      /**/
      stage: [],
      companyTitle:"",
      queryData:{},

    }
  },
  methods: {
    /**/
    getCity(data){
      let arr = [];
      for(let i=0; i<data.length; i++){
        let obj={};
        obj.label=data[i].area_title;
        obj.value=data[i].area_id;
        arr.push(obj)
      }
      return arr
    },//
    getScale(data){
      let arr = [];
      for(let i=0; i<data.length; i++){
        let obj={};
        obj.label=data[i].scale_money;
        obj.value=data[i].scale_id;
        arr.push(obj)
      }
      return arr
    },//
    getStage(data){
      let arr = [];
      for(let i=0; i<data.length; i++){
        let obj={};
        obj.label=data[i].stage_name;
        obj.value=data[i].stage_id;
        arr.push(obj)
      }
      return arr
    },//
    getIndustry(data){
      let arr = [];
      for(let i=0; i<data.length; i++){
        let obj={};
        obj.label=data[i].industry_name;
        obj.value=data[i].industry_id;
        arr.push(obj)
      }
      return arr
    },//
    getWxProjectCategory(){
      this.$http.post(this.URL.getWxProjectCategory,{user_id: sessionStorage.user_id})
        .then(res=>{
          let data = res.data.data;
          this.area=this.getCity(data.area);//1
          this.scale=this.getScale(data.scale);//
          this.stage=this.getStage(data.stage);//
          this.industry=this.getIndustry(data.industry);//
        })
        .catch(err=>{
          console.log(err)
          //            this.loading=false;
        })
    },//
    area1Change(data){
      this.project.pro_area_city="";
      this.$http.post(this.URL.getArea,{user_id: sessionStorage.user_id, pid:data})
        .then(res=>{
          let data = res.data.data;
          this.area2=this.getCity(data);
        })
        .catch(err=>{
          console.log(err)
        })
    },//


    /**/
    planChange(file, fileList){
      this.planList=fileList
      if(file.status==="fail") this.planButton=true;
      else this.planButton=false;
    },
    planuploadsuccess(response, file, fileList){
      this.success("");
      console.log(response)
      let data = response.data;
      this.addplan(data.file_title,data.pro_desc,data.pro_name,data.project_id,data.file_id)
    },//
    planuploaderror(err, file, fileList){
      this.alert(",")
    },//
    planRemove(file, fileList) {
      const deleteAtUpload=this.URL.deleteAtUpload;
      if (fileList.length == 0) this.planButton = true;
      else this.planButton = true;
      this.$http.post(deleteAtUpload,{user_id: sessionStorage.user_id,project_id:this.uploadShow.project_id})
        .then(res=>{
          if(res.status===200){
            this.loading=false;
            this.success("")
          }
          console.log(res)
        })
        .catch(err=>{
          console.log(err)
          this.alert(",")
        })

    },//
    addplan(file_title,pro_desc,pro_name,project_id,file_id) {
      let object ={};
      object.file_title=file_title;
      object.pro_desc=pro_desc;
      object.pro_name=pro_name;
      object.project_id=project_id;
      object.file_id=file_id;
      this.uploadShow=object;
    },//,
    planPreview(file){
      console.log(this.uploadShow);
      const download=this.URL.download;
      this.$http.get(download,{ params:{user_id: sessionStorage.user_id,file_id:this.uploadShow.file_id}})//this.uploadShow.file_id
        .then(res=>{
          if(res.data.status_code===4004004){
            this.loading=false;
            this.alert(",")
          }
          console.log(res)
        })
        .catch(err=>{
          console.log(err)
          this.opealertn(",")
        })
    },//
    beforeUpload(file){
      if(parseInt(file.size) > parseInt(31457281)){
        this.alert("30MB");
        return false;
      }
    },//


    /**/
    alert(text) {
      this.$notify.error({
        message: text,
        offset: 300,
        duration:1000
      });
    },
    /**/
    success(text) {
      this.$notify({
        message: text,
        type: 'success',
        offset: 300,
        duration:1000
      })
    },
    goBack(){//
      this.$router.go(-1);
    },
    /**/
    submitForm(formName) {
      let check=true
      this.$refs[formName].validate((valid) => {
        if (valid) {
          return
        } else {
          this.alert('')
          check=false;
        }
      });
      return check;
    },
    /**/
    open2(title,main,confirm,cancel) {
      this.$confirm(main,title , {
        confirmButtonText: confirm,
        cancelButtonText:cancel ,
        type: 'success'
      }).then(() => {
        this.$message({
          type: 'success',
          message: ''
        });
        this.$router.push({ name: 'editproject', query: {project_id:this.project.project_id}})
      }).catch(() => {
          console.log()
        this.$router.push({ path: '/workBench/myProject'})
      });
    },
    /**/
    allSave(){
      if(this.submitForm('project')) {
        this.project.user_id=sessionStorage.user_id;
        this.project.project_id=this.uploadShow.project_id;
        this.$http.post(this.URL.editProject,this.project)
          .then(res=>{
            console.log(res);
            let data=res.data;
            this.project.project_id=data.project_id;
            this.open2('','','','')
          })
          .catch(err=>{
            this.alert("");
            console.log(err);
          })

      }
      console.log(this.$tool.getToObject(this.project));
    },
    /**/
    loadData(arr){
      let newArr=[];
      for(let i=0; i<arr.length; i++){
        let obj={};
        obj.value=arr[i].company_name;
        obj.address=arr[i].com_id;
        newArr.push(obj)
      }
      return newArr
    },
    /*,*/
    querySearchAsync(queryString, cb) {
      this.$http.post(this.URL.selectCompany,{user_id:sessionStorage.user_id,company_name:queryString})
        .then(res=>{
          this.restaurants=[];
          let data=res.data.data;
          this.restaurants=this.loadData(data);
          if(queryString=="") this.restaurants=[];
          let restaurants = this.restaurants;
          let results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;
          clearTimeout(this.timeout);
          this.timeout = setTimeout(() => {
            cb(results);
          }, 300);
        })
        .catch(err=>{
          this.alert("");
          console.log(err);
        })
    },
    createStateFilter(queryString) {
      return (state) => {
        return (state.value.indexOf(queryString.toLowerCase()) === 0);
      };
    },
    handleSelect(item) {
      this.companyTitle=item.value;
      this.$http.post(this.URL.getOneCompany,{user_id:sessionStorage.user_id,com_id:item.address})
        .then(res=>{
          let data=res.data.data;
          this.queryData=data;
//          console.log(this.$tool.getToObject(data));
        })
        .catch(err=>{
          this.alert("");
          console.log(err);
        });
     this.dialogVisible=true;
    },
    /**/
    sync(){
      this.dialogVisible = false;
      this.project.pro_intro=this.queryData.project_info.project_introduce;
      this.project.pro_company_name=this.queryData.company_name;
    },
    getprojectId(){
      this.project.project_id = this.$route.query.project_id;
      console.log(this.$route.query.project_id);
    },
    getWxosProjectData(){
      console.log("")
      console.log(sessionStorage.credential);
      if(sessionStorage.credential==undefined || sessionStorage.credential==""){

      }else{
        console.log("")
        this.$http.post(this.URL.getWxosProjectData,{credential:sessionStorage.credential})
          .then(res=>{
            let data=res.data.project;
            console.log(this.$tool.getToObject(data));
            this.project.pro_industry=data.industry;
            if(data.is_exclusive==4) data.is_exclusive=0;
            this.project.is_exclusive=data.is_exclusive;
            this.project.pro_finance_scale=data.pro_finance_scale;
            this.project.pro_finance_stage=data.pro_finance_stage;
            this.project.pro_goodness=data.pro_goodness;
            this.project.pro_intro=data.pro_intro;
            sessionStorage.credential="";
            console.log(this.$tool.getToObject(this.project))
          })
          .catch(err=>{
            this.alert("");
            console.log(err);
          });
      }
    }//
  },
  mounted() {

  },
  created(){
    if(this.planList.length!=0) this.planButton=false;
    else this.planButton=true;
    this.getprojectId();
    this.getWxProjectCategory();
    this.getWxosProjectData();
  },
/*watch: {
  // 
  "$route": "getWxosProjectData"
}*/

}
</script>

<style lang="less">
  @import '../../../assets/css/edit.less';
  .Upload{
    .el-upload{

    }
    .el-upload-list__item{
      /*    width: 200px;*/
    }
    .el-upload-list__item-name{
      font-size:14px;
      color:#475669;
      letter-spacing:0;
      text-decoration:underline
    }
    .el-upload__tip{
      width:539px;
      margin-left: 19px;
      height: 36px;
      line-height: 36px;
      font-size:12px;
      color:#5e6d82;
    }
    .el-upload__tip{
      margin-top: 0;
    }

  }
  .el-dialog--tiny{
    width: 390px;
  }
  .el-dialog__body{
    font-size:14px;
    color:#475669;
    letter-spacing:0;
    line-height:21px;
  }
  .el-upload-list__item:first-child{
    margin-top:0px;
  }
  .el-upload-list__item{
    margin-top:0px;
    line-height:1;
  }
</style>



